      width: 300px;
      padding-left: 20px;
    }
    .search {
      position: relative;
      padding-left: 20px;

      i {
        position: absolute;
        top: 50%;
        left: 10px;
        z-index: 4;
        color: #8e9dba;
        transform: translateY(-50%);
      }
      input {
        background-color: transparent;
        border: 0;
        width: 100%;
        height: 100%;
      }
      input:focus {
        border: 0;
        outline: none;
      }
      .showBtn {
        position: absolute;
        top: 50%;
        right: 10px;
        z-index: 4;
        color: white;
        border-radius: 20px;
        font-size: 12px;
        border: 1px solid white;
        padding: 3px 5px;
        line-height: 20px;
