.dashboard-market {

  .dashboard-market-item {
    border-right: 2px dashed #0188e4;

  }
  .dashboard-market-item:last-child {
    border-right: none;
  }
}

.cooler-cards {
  @include modalBack;

  border-radius: 20px;
  padding: 30px;

  .cooler-card {
    border-right: 2px dashed #0188e4;
  }
  .cooler-card:last-child {
    border-right: none;
  }
}

.coolerModal {
  @include modalBack;
  @include modalBoxShadow;
  @include modalAnimation;

  padding: 30px;
  border-radius: 20px;
  max-width: 500px;
  width: 100%;
  margin-left: auto;
  margin-right: auto;

  h1, p{
    text-align: center;
  }
}

p, h4, h5, h6, label{
  font-family: cursive !important;
}
h1, h2{
  font-family: monospace !important;
}

.ending {
  .container {
    @include modalAnimation;
    @include modalBack;
    @include modalBoxShadow;

    padding: 30px;
    border-radius: 20px;
  }
}


.navbar-header {
  h1{
    font-family: fantasy !important;
    color: white;
    font-size: 50px;
    letter-spacing: 4px;
  }
  p{
    font-family: cursive;
    color: wheat;
    font-style: italic;
  }
}

.botto {
  @include modalAnimation;
  @include modalBack;
  @include modalBoxShadow;
  border-radius: 20px;
}
.partner {
  @include modalBack;
}

.brightMode{
  .navbar-header {
    h1{
      text-shadow: 2px 0 #0188e4, -2px 0 #0188e4, 0 2px #0188e4, 0 -2px #0188e4;
    }
    p{
      color: rgb(46, 46, 46);
    }
  }
}

.moveleft {
  animation: moveleft 1s ease-in-out;
}

@keyframes moveleft {
  0%{
    transform: translateX(-100%);
  }
  100%{
    transform: translateX(0);
  }
}
.moveright {
  animation: moveright 1s ease-in-out;
}

@keyframes moveright {
  0%{
    transform: translateX(100%);
  }
  100%{
    transform: translateX(0);
  }
}
.movetop {
  animation: movetop 1s ease-in-out;
}

@keyframes movetop {
  0%{
    transform: translateY(-100%);
  }
  100%{
    transform: translateY(0);
  }
}


@media (max-width : 565px) {
  .p40{
    padding: 10px;
  }
  .p0-40{
    padding: 10px;
  }
  .stackingModal {
    padding-inline-start: 30px 10px;
    label {
      font-size: 13px;
    }
  }
  .dashboard-market {
    margin-top: 120px;
    .dashboard-market-item {
      border: 0;
      border-bottom: 2px dashed #0188e4;
      padding: 20px 0;

      &:last-child {
        border-bottom: none;
      }
    }
  }
  .cooler-lists {
    margin-top: 130px;
  }
  .cooler-card {
    border-right: none !important;
    border-bottom: 2px dashed #0188e4;
    padding: 20px 0;

    &:last-child {
      border-bottom: none;
    }
  }
  .CoolerBorrowContainer  .button-64{
    font-size: 10px;
  }
  .rangeFirst{
    margin-top: 220px;
  }
  .price-range {
    input {
      max-width: 450px;
      width: 100%;
    }
    span{
      font-size: 10px;
    }
  }
}

.welcome {
  .items {
    .item{
      @include modalBack;
      @include modalBoxShadow;

      border-radius: 20px;
    }
  }
}
.brightMode{ 
  
  .cooler-middle {
    h4{
      color: white;
    }
  }
  .cooler-container{
  
    .cooler-card{
      h4{
        color: white;
      }
    }
    .coolerModal {
      h1, p{
        color: white;
      }
    }
  }
  .rangeModal {
    h3{
      color: white;
    }
  }
}